import PropTypes from "prop-types";
import Counter from "../../../components/Counter";

import styles from "../components/index.module.scss"

const Task = ({ 
  count, 
  addCounter, 
  resetCount,
  handleIncrement,
  handleDecrement,
  handleDelete,
  handleReset,
  totalSum,
}) => {

  return(
    <div className={styles.wrapperCount}>
      
      <div className={styles.buttonsArea}>
        <button onClick={addCounter} className={styles.mainButtons}>Add Counter</button>
        <button onClick={resetCount} className={styles.mainButtons}>Reset Counter</button> 
      </div>
      
      <h2>
        <p>    : {count.length}</p>
        <p>    : {totalSum}</p>
      </h2>

      <div>  {/*       */}
        {
          count.map(({ id, countValue }) => (
            <Counter 
              id = {id}
              key = {id}  /*          */
              countValue = {countValue} 
              onIncrement = {handleIncrement}
              onDecrement = {handleDecrement}
              onDelete = {handleDelete}
              onReset = {handleReset}
            />
          ))
        }
      </div>
    </div>
  )
}

Task.propTypes = {
  count: PropTypes.arrayOf(
    PropTypes.shape({id: PropTypes.string, countValue: PropTypes.number}) 
    // shape -  ? -       ,     .   ,         .
    // ,       
    // (      ).
  ).isRequired,
}

export default Task;

