import { useState, useCallback } from 'react';
import { v4 as uuid } from 'uuid';

import Task from '../components'; // Task ..       task

const TaskCounterContainer = () => { 
    const [count, setCount] = useState([]); 
//      useState([]) c    countValue

    const addCounter = () => {
        const newCounter = {  //    id
            id: uuid(),
            countValue: 0, 
        }
        //setCount([...count, newCounter]) //     count,   
        
        setCount((state) => { //            + 1    
            const update = state.map((counter) => {
                return {
                    ...counter,
                    countValue: counter.countValue % 2 === 0 ? counter.countValue + 1 : counter.countValue,
                }
            })
            return [...update, newCounter]
        })
    }

    const resetCount = useCallback(() => {
        setCount([])
    }, [])

    const handleIncrement = useCallback((id) => {
        setCount((state) => {
            const countCopy = [...state]  //   
            const findElement = countCopy.find( //    countCopy    
                (counter) => counter.id === id
            )
            findElement.countValue += 1 //        countValue         .. += 1

            return countCopy //   
        })
    }, [])

    const handleDecrement = useCallback((id) => {
        setCount((state) => {
            const countCopy = [...state]
            const findElement = countCopy.find(
                (counter) => counter.id === id
            )

            if (findElement.countValue > 0) {findElement.countValue -= 1};

            return countCopy //   
        })
    }, [])

    const handleDelete = useCallback((id) => {
        setCount((state) => {
            const countCopy = [...state]
            const deleteElement = countCopy.findIndex( // findIndex     
                (counter) => counter.id === id
            )

            countCopy.splice(deleteElement, 1)
            
            //   
            return countCopy.map(({id, countValue}) => {
                return{
                    id,
                    countValue: countValue % 2 !== 0 ? countValue - 1 : countValue
                }
            })
        })
    }, [])

    const handleReset = useCallback( (id) => {
        setCount((state) => {
            const countCopy = [...state]
            const findElement = countCopy.find(
                (counter) => counter.id === id
            )
            findElement.countValue = 0
            return countCopy
        })
    }, [])

    const totalSum = count.reduce( ( sum, {countValue} ) => { // {countValue} -    , ..   
            return sum + countValue
        }, 0)

    return(
        <Task 
            count= {count}
            addCounter = {addCounter}
            resetCount = {resetCount}
            handleIncrement = {handleIncrement}
            handleDecrement = {handleDecrement}
            handleDelete = {handleDelete}
            handleReset = {handleReset}
            totalSum = {totalSum}
        />
    )
}

export default TaskCounterContainer;



